import { GET_ADS_CATEGORY_LIST } from "@/api/api";
import dashboardAdsImg from '@/assets/ads/2.png';
import meSideMenu from "@/components/meSideMenu";
export default {
  name: "platform",
  components: {
    meSideMenu
  },

  data() {
    return {
      dashboardAdsImg,
      adsCategoryData: []
    };
  },

  mounted() {
    this.getAdCategoryList();
  },

  methods: {
    selectPlatform(id, key) {
      if (key == 'App') {
        this.$router.push({
          path: '/me/ads/identity',
          query: {
            platform_id: id,
            inApp: 1
          }
        });
      } else {
        this.$router.push({
          path: '/me/ads/list',
          query: {
            platform_id: id,
            inApp: 2
          }
        });
      }
    },

    turnBack() {
      this.$router.push('/me/ads');
    },

    getAdCategoryList() {
      let params = {
        token: localStorage.getItem('token'),
        tree: 1
      };
      GET_ADS_CATEGORY_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.adsCategoryData = res.message;
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    }

  }
};